import React, { useState } from 'react';
import styles from './index.module.css';

const Top = ({ searchV }) => {
    const valS = a =>searchV(a)
    return (
        <nav className={styles.nav1}>
            <p>MOVIES</p>
            <div className={styles.inputW}>
                <input
                    type="text"
                    placeholder="Enter Movie name..."
                    onKeyUp={e=>valS(e.target.value)}
                />
            </div>
        </nav>
    );
}

export default Top;